

















































import { Component, Vue, Prop } from 'vue-property-decorator'

@Component({
  name: 'UiBox'
})
export default class UiBox extends Vue {
  @Prop({ required: true, type: [String] })
  contentId!: string

  @Prop({ required: true, type: [String] })
  type!: string

  @Prop({ required: true, type: [String] })
  arrow!: string

  @Prop({ type: [String] })
  icon!: string

  get typeClass () {
    switch (this.type) {
      case 'info':
        return `${this.arrow}-arrow bg-blue-100 text-blue-500`
      case 'warning':
        return `${this.arrow}-arrow bg-orange-100 text-orange-500`
      default:
        throw new Error('UI Box type not recognised')
    }
  }

  get iconClass () {
    switch (this.icon) {
      case 'recommendation':
        return 'lightbulb'
      case 'ram':
        return 'sd-card'
      default:
        throw new Error('UI Box icon type not recognised')
    }
  }
}

